
.main {

    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;


}


.dialogs {
    /*display: grid;*/
    /*grid-template-columns: 2fr 10fr ;*/
    display: flex;
    justify-content: center;
    text-align: center;
    font-size: 20px;
}

.usersList {
    padding: 10px;
}


.user a {
    color: #4b4a4a;
    text-decoration: none;
    padding: 10px;
}

.user a:active {
    color: #f1d6d6;

}

.user a:hover {
    text-decoration: none;
    color: #e2dada;
}


.messages {
    padding: 10px;
    font-size: 20px;


}

.dialogs_message {
    height: 680px;
    overflow-y: auto;

}

