.img {
    width: 800px
}


.addMessageBorder {

    width: 100%;
    padding: 5px;

    border: #e2dada 1px solid;
    border-radius: 6px;
}


#talkbubble {
    width: 260px;
    padding: 5px;
    background: rgba(226, 218, 218, 0.24);
    color: #666464;
    position: relative;
    border-radius: 10px;
    margin-left: 20px;

    align-items: center;
    justify-content: space-between;
    display: flex;
}

#talkbubble:before {
    content: "";
    position: absolute;
    right: 100%;


    border-top: 13px solid transparent;
    border-right: 26px solid rgba(226, 218, 218, 0.42);;
    border-bottom: 13px solid transparent;
}


.one {
    flex: 1;
    padding-left: 0px;
}

.two {
    flex: 2
}

.item {
    margin: 10px 0px 5px 15px;
    flex-grow: 2;
}

.messages_item {
    height: 500px;
    overflow-y: auto;

    flex-grow: 2;
}



@media (max-width: 500px) {
    #talkbubble {
        width: 70%;
        height: 70%;
    }

    .item img {
        width: 70%;
        height: 70%;
        border-radius: 5px;
    }

    #talkbubble:before {
        display: none;
    }


    span, h4 {
        font-size: 70%;
    }

    .two {
        text-align: center;
    }

    .logo {
        width: 10px;
        height: 10px;
    }

    .messages_item {
        height: 500px;
        overflow-y: auto;
    }
}


@media (max-width: 375px) {

    .messages_item {
        height: 300px;
        overflow-y: auto;
    }

}

@media (max-width: 360px) {

    .messages_item {
        height: 300px;
        overflow-y: auto;
    }

}

@media (max-width: 250px) {


    .messages_item {
        height: 60px;
        overflow-y: auto;
    }

}


@media (max-width: 1100px) {

    .messages_item {
        height: 260px;
        overflow-y: auto;
    }

}